.staff-page {
    background: #f4f6f9;
    padding: 20px;
    font-family: 'Poppins', sans-serif;
    padding-top: 5px; 
     overflow-y: hidden;
}


.staff-container {
    max-width: 1400px;
    margin: 0 auto;
}

.staff-header {
    margin-bottom: 20px;
    /* padding-top: 5px; */
   
}


.float {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
    background-color: #007bff;
    border-radius: 50%;
    box-shadow: 0 4px 10px rgba(0, 123, 255, 0.35);
    cursor: pointer;
    transition: all 0.3s ease;
}

.float i {
    color: #ffffff;
    font-size: 18px;
}

.float:hover {
    background-color: #0056b3;
    transform: translateX(-3px);
    box-shadow: 0 6px 14px rgba(0, 123, 255, 0.45);
}

.my-float {
    margin: 0 !important;
}

.staff-columns {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.staff-column {
    width: 100%;
}

.staff-card {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
    height: 100%;
}

.staff-card-body {
    padding: 25px;
}

.staff-card-title {
    font-size: 20px;
    font-weight: 600;
    color: #333;
    margin-bottom: 25px;
    padding-bottom: 15px;
    border-bottom: 2px solid #f0f0f0;
}

.staff-fields {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.staff-group {
    display: flex;
    flex-direction: column;
}

.staff-group label {
    font-size: 13px;
    font-weight: 600;
    margin-bottom: 6px;
    color: #555;
}

.staff-label-normal {
    font-size: 13px;
    font-weight: 600;
    margin-bottom: 6px;
    color: #555;
}

.staff-required {
    color: red;
}

.staff-group input,
.staff-group select,
.staff-group textarea {
    width: 100%;
    padding: 10px 12px;
    border-radius: 6px;
    border: 1px solid #ddd;
    font-size: 14px;
    transition: all 0.3s ease;
}

.staff-group input:focus,
.staff-group select:focus,
.staff-group textarea:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
}

.staff-group-full {
    grid-column: 1 / -1;
}

.staff-group-full textarea {
    min-height: 80px;
    resize: vertical;
    font-family: inherit;
}


.staff-btn-container {
    display: flex;
    justify-content: center;
    margin-top: 15px;
    padding: 10px 0;
}

.staff-btn {
    padding: 12px 50px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 6px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.staff-btn:hover {
    background-color: #0056b3;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(0, 123, 255, 0.3);
}

.staff-btn:active {
    transform: translateY(0);
}

.staff-heading-divider {
    width: 100%;
    height: 1px;
    background-color: #e5e7eb;
    margin-top: 12px;
    margin-bottom: 20px;
}

@media (max-width: 992px) {
    .staff-columns {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 768px) {
    .staff-fields {
        grid-template-columns: 1fr;
    }

    .staff-card-body {
        padding: 20px;
    }
}

@media (max-width: 480px) {
    .staff-page {
        padding: 15px 10px;
    }
}