.container {
  margin-top: 130px;
}


.table-responsive {
  overflow-x: visible !important;
}

.table-responsive table {
  width: 100% !important;
  table-layout: fixed;
}

.dataTables_wrapper {
  width: 100% !important;
  overflow-x: visible !important;
}
.plus-icon:focus:not(:focus-visible) {
  outline: none;
  box-shadow: none;
}

/* Modal wrapper */
.modal-size.md.modal-top-center .modal-content {
  background-color: #ffffff;
  padding: 2rem;
  border-radius: 10px;
  width: 400px;
  max-width: 90%;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
}

/* Modal Header */
.modal-size.md.modal-top-center .modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: none;
  padding: 0;
  margin-bottom: 1rem;
}

.modal-size.md.modal-top-center .modal-header h6 {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 600;
}

/* Close button */
.modal-size.md.modal-top-center .modal-header span {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  transition: color 0.2s ease;
}

.modal-size.md.modal-top-center .modal-header span:hover {
  color: #dc3545; /* Red on hover */
}

/* Modal Body */
.modal-size.md.modal-top-center .modal-body {
  padding: 0;
}

/* Form styling */
.modal-size.md.modal-top-center input,
.modal-size.md.modal-top-center select {
  font-size: 1rem;
  padding: 0.5rem;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.modal-size.md.modal-top-center label {
  font-size: 1rem;
  color: #000;
  font-weight: 500;
  margin-bottom: 0.25rem;
  display: block;
}

.modal-size.md.modal-top-center .input-group-append button {
  border-radius: 0 5px 5px 0;
}

/* Modal Footer */
.modal-size.md.modal-top-center .modal-footer {
  border-top: none;
  padding: 1rem 0 0 0;
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
}

.plus-icon:active {
  background-color: transparent !important;
  border: 1px solid #28a745 !important;
  box-shadow: none !important;
}

