/* Base layout */
.dashboard-layout {
  display: flex;
  height: 100vh;
  width: 100%;
  position: relative;
}

/* Sidebar */
.sidebar {
  width: 240px;
  background: #333;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  color: white;
  z-index: 1000;
  transition: all 0.3s ease;
}

.sidebar.collapsed {
  width: 70px;
}

/* Main wrapper */
.main-wrapper {
  margin-left: 240px;
  width: calc(100% - 240px);
  transition: all 0.3s ease;
  flex: 1;
  min-width: 0;
}

/* Header/Topbar */
.header {
  position: fixed;
  top: 0;
  left: 240px;
  width: calc(100% - 240px);
  height: 60px;
  background: #fff;
  border-bottom: 1px solid #ddd;
  z-index: 999;
  transition: all 0.3s ease;
}

/* Content area */
.content {
  margin-top: 80px;
  padding: 24px;
  background: #f5f6fa;
  min-height: calc(100vh - 80px);
}

/* Collapsed sidebar state */
.sidebar-collapsed .main-wrapper {
  margin-left: 70px;
  width: calc(100% - 70px);
}

.sidebar-collapsed .header {
  left: 70px;
  width: calc(100% - 70px);
}

/* Hamburger menu */
.nav-control {
  display: flex;
  align-items: flex-start;
  margin-left: 15px;
  z-index: 1001;
}

.nav-control .hamburger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 18px;
  cursor: pointer;
}

.nav-control .hamburger .line {
  height: 2px;
  width: 100%;
  background-color: #333;
  border-radius: 1px;
  transition: all 0.3s ease;
}

.nav.align-items-center li {
  padding-top: 20px;
}

/* Overlay for mobile sidebar */
.sidebar-overlay {
  display: none;
}

/* ========== RESPONSIVE BREAKPOINTS ========== */

/* Tablet (768px - 1023px) */
@media (max-width: 1023px) {
  .sidebar {
    transform: translateX(-100%);
  }

  .dashboard-layout:not(.sidebar-collapsed) .sidebar {
    transform: translateX(0);
  }

  .main-wrapper {
    margin-left: 0;
    width: 100%;
  }

  .header {
    left: 0;
    width: 100%;
  }

  .sidebar-collapsed .main-wrapper {
    margin-left: 0;
    width: 100%;
  }

  .sidebar-collapsed .header {
    left: 0;
    width: 100%;
  }

  /* Show overlay when sidebar is open on mobile/tablet */
  .dashboard-layout:not(.sidebar-collapsed) .sidebar-overlay {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
  }

  .content {
    padding: 16px;
  }
}

/* Mobile (up to 767px) */
@media (max-width: 767px) {
  .content {
    padding: 12px;
    margin-top: 70px;
  }

  .header {
    height: 56px;
  }

  .nav-control {
    margin-left: 10px;
  }

  .nav-control .hamburger {
    width: 26px;
    height: 16px;
  }
}

/* Small mobile (up to 480px) */
@media (max-width: 480px) {
  .sidebar {
    width: 80%;
    max-width: 280px;
  }

  .content {
    padding: 8px;
  }
}

/* Desktop large (1440px+) */
@media (min-width: 1440px) {
  .content {
    padding: 32px;
  }
}