body {
  margin: 0;
  background-color: #f5f6fa;
}
html {
  font-size: 80%; 
}

.unix-login {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 1rem;
}


.login-content {
  background: #ffffff;
  padding: 2.5rem;
  border-radius: 8px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 420px;
}


.login-form h4 {
  text-align: center;
  margin-bottom: 2rem;
}


.text-danger {
  color: #e53935;
  font-size: 0.9rem;
  margin-top: 0.3rem;
}


.login-form button {
  width: 100%;
  background-color: #28292c; 
  border-color:#28292c;
}

.login-form button:hover {
  background-color: #21242b;
  border-color: #21242b;
}


.password-toggle {
  cursor: pointer;
  font-size: 0.9rem;
  color: #666;
}

.float {
  position: fixed;
  width: 60px;
  height: 60px;
  bottom: 40px;
  right: 20px;
  background-color: #3f29b8;
  color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 10px rgba(0,0,0,.2);
  z-index: 999;
}
.password-toggle {
  cursor: pointer;
  opacity: 0.6;
}

.password-toggle:hover {
  opacity: 1;
}

.login-form input {
  width: 100%;
}
